ks-order-container {
    display: block;
    padding: 0px 30px;
    margin: 30px 0px;
}

@media (min-width: 1200px) {
    ks-order-container {
        padding: 0px 40px;
        margin: 40px 0px;
    }
}

@media (max-width: 720px) {
    ks-order-container {
        padding: 0px 15px;
        margin: 40px 0px;
    }
}